.main-slider {
  margin-top: 15px;
  margin-bottom: 15px;
  position: relative;
}

.main-slider__slide img {
  width: 100%;
  height: 800px;
  object-fit: cover;
}

.slider-dots {
  width: 100px;
  justify-content: space-between;
  display: flex;
  position: absolute;
  bottom: 5%;
  left: 45%;
}

.slider-dots__dot {
  width: 16px;
  height: 16px;
  background-color: #fff;
  border-radius: 100%;
}

.slider-dots__dot--active {
  background-color: #f34e21;
}

.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {
    opacity: .1;
  }

  to {
    opacity: 1;
  }
}

@media (max-width: 768px) {
  .main-slider__slide img {
    height: 80%;
  }
}

@media (max-width: 480px) {
  .slider-dots {
    left: 38%;
  }

  .slider-dots__dot {
    width: 12px;
    height: 12px;
  }
}



